import React, { useState, useEffect } from 'react';

import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import '@vkontakte/vkui/dist/vkui.css';

import { Panel, PanelHeader, PanelHeaderClose, Group,  Button, Header, Root} from '@vkontakte/vkui';

import './css/main.css'
import App from '../App';
import {Ur, SysUr, KvUr, OpDr, UprVr, OkrCh, PrOtCh, PlV, Stl, Mpv, Ek} from './calculators'

class Main extends React.Component {
    constructor(props) {
        super(props);
    
        this.state = {
          activePanel: App.Calc,
          activeView: this.props.View
        }
      }

      setCalc(calc){
          App.Calc = calc;
          const {SetBar} = this.props;
          SetBar();
      }
    
    render () { 
        const {SetBar} = this.props;
        return (
            <Root activeView={this.state.activeView}>
                <View id="main" header = {false}>
                        <Group header={<Header mode="secondary">Математика</Header>}>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc1')} size="xl" mode="secondary">Линейное уравнение</Button>
                            </div>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc2')} size="xl" mode="secondary">Система уравнений</Button>
                            </div>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc3')} size="xl" mode="secondary">Квадратное уравнение</Button>
                            </div>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc4')} size="xl" mode="secondary">Операции с дробями</Button>
                            </div>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc5')} size="xl" mode="secondary">Упростить выражение</Button>
                            </div>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc6')} size="xl" mode="secondary">Округление чисел</Button>
                            </div>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc7')} size="xl" mode="secondary">Процент от числа</Button>
                            </div>
                        </Group>

                        <Group header={<Header mode="secondary">Физика</Header>}>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc8')} size="xl" mode="secondary">Плотность веществ</Button>
                            </div>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc9')} size="xl" mode="secondary">Скорость, время и расстояние</Button>
                            </div>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc10')} size="xl" mode="secondary">Масса, плотность и объём</Button>
                            </div>
                            <div className = "BtnList">
                                <Button onClick={() => this.setCalc('calc11')} size="xl" mode="secondary">Кинетическая энергия</Button>
                            </div>
                        </Group>
                        
                        <Group header={<Header mode="secondary">Текстовые и числовые калькуляторы</Header>}>
                            <div className = "BtnList">
                                <Button size="xl" mode="secondary">Количество символов</Button>
                            </div>
                            <div className = "BtnList">
                                <Button size="xl" mode="secondary">Системы счисления</Button>
                            </div>
                            <div className = "BtnList">
                                <Button size="xl" mode="secondary">Распределение чисел</Button>
                            </div>
                        </Group>
                </View>

                <View id="calcs" activePanel={this.state.activePanel}>
                    <Panel id="calc1">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Линейное уравнение
                        </PanelHeader>
                        
                        <Ur/>
                    </Panel>
                    
                    <Panel id="calc2">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Система уравнений
                        </PanelHeader>
                        
                        <SysUr/>
                    </Panel>
                    
                    <Panel id="calc3">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Квадратное уравнение
                        </PanelHeader>
                        
                        <KvUr/>
                    </Panel>

                    <Panel id="calc4">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Операции с дробями
                        </PanelHeader>
                        
                        <OpDr/>
                    </Panel>
                    <Panel id="calc5">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Упростить выражение
                        </PanelHeader>
                        
                        <UprVr/>
                    </Panel>

                    <Panel id="calc6">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Округление чисел
                        </PanelHeader>
                        
                        <OkrCh/>
                    </Panel>

                    <Panel id="calc7">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Процент от числа
                        </PanelHeader>
                        
                        <PrOtCh/>
                    </Panel>

                    
                    <Panel id="calc8">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Плотность веществ
                        </PanelHeader>
                        
                        <PlV/>
                    </Panel>

                    <Panel id="calc9">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Скорость, время, расстояние
                        </PanelHeader>
                        
                        <Stl/>
                    </Panel>

                    <Panel id="calc10">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Масса, плотность, объём
                        </PanelHeader>
                        
                        <Mpv/>
                    </Panel>

                    <Panel id="calc11">
                    <PanelHeader
                        left={<PanelHeaderClose onClick={SetBar} />}
                        >
                        Кинетическая энергия
                        </PanelHeader>
                        
                        <Ek/>
                    </Panel>

                </View>
            </Root>

            );
        }
      }
    
    
    
    export default Main;