class Stl extends React.Component{
  constructor(props) {
      super(props);
  
      this.state = {
          whFind: " ",
          result: ' '
      }
      this.urInput1 = null;
      this.urInput2 = null;

    }
    whChange = event => {
      this.setState({whFind: event.target.value})
    }    
    handleChange1 = event => {
      this.urInput1 = event.target.value;
    }
    handleChange2 = event => {
      this.urInput2 = event.target.value;
    }

    calc = () =>{  
      try{
          let a;
          switch(this.state.whFind){
            case "V":
              a = this.urInput1 / this.urInput2;
              break;
            case "S":
              a = this.urInput1 * this.urInput2;
              break;
            case "t":
              a = this.urInput1 / this.urInput2;
              break;
        }
          this.setState({result: this.state.whFind + " = " + a});
        }
      catch (e) {
        this.setState({result: "Ошибка! Введены неверные значения"});
        console.log(e)
      }
    }
  render(){
    let wh1 = " ";
    let wh2 = " ";
    switch(this.state.whFind){
      case "V":
        wh1 = "Введите расстояние";
        wh2 = "Введите время";
        break;
      case "S":
        wh1 = "Введите скорость";
        wh2 = "Введите время";
        break;
      case "t":
        wh1 = "Введите расстояние";
        wh2 = "Введите время";
        break;
  }
      return(
          <div className="CalcDiv">    
              <Group>
              <Card size="l" mode="shadow" style={{height:285}}>
              <Select placeholder="Что будем находить?" onChange={this.whChange}>
                <option value="V">Скорость</option>
                <option value="t">Время</option>
                <option value="S">Расстояние</option>
              </Select>
              <Input type="text" style = {{marginTop:10}} placeholder={wh1} size="xl" onChange={this.handleChange1}/>  
              <Input type="text" style = {{marginTop:5}} placeholder = {wh2}  size="xl" onChange={this.handleChange2}/>   
              <Button onClick={this.calc} size="xl" className="CalcBtn" mode="commerce">Вычислить</Button>
              <h1 className="CalcsRes">{this.state.result}</h1>
              </Card>
              </Group>
              <Group> <p><strong>S = V*t</strong></p>
              <p><strong>S</strong> - расстояние</p>
              <p><strong>V</strong> - скорость</p>
              <p><strong>t</strong> - время</p>
              </Group>
          </div>
      );
  }  
}

