class Ek extends React.Component{
  constructor(props) {
    super(props);

    this.state = {
        whFind: " ",
        result: ' '
    }
    this.urInput1 = null;
    this.urInput2 = null;

  }
  whChange = event => {
    this.setState({whFind: event.target.value})
  }    
  handleChange1 = event => {
    this.urInput1 = event.target.value;
  }
  handleChange2 = event => {
    this.urInput2 = event.target.value;
  }

  calc = () =>{  
    try{
        let a;
        switch(this.state.whFind){
          case "E":
            a = this.urInput1 * Math.pow(this.urInput2, 2) / 2;
            break;
          case "m":
            a = 2 * this.urInput1 / Math.pow(this.urInput2, 2);
            break;
          case "V":
            a = Math.sqrt(2 * this.urInput1 / this.urInput2);
            break;
      }
        this.setState({result: this.state.whFind.replace("E") + " = " + a});
      }
    catch (e) {
      this.setState({result: "Ошибка! Введены неверные значения"});
      console.log(e)
    }
  }
render(){
  let wh1 = " ";
  let wh2 = " ";
  switch(this.state.whFind){
    case "E":
      wh1 = "Введите массу";
      wh2 = "Введите скорость";
      break;
    case "m":
      wh1 = "Введите кинетическую энергию";
      wh2 = "Введите скорость";
      break;
    case "V":
      wh1 = "Введите кинетическую энергию";
      wh2 = "Введите массу";
      break;
}
    return(
        <div className="CalcDiv">    
            <Group>
            <Card size="l" mode="shadow" style={{height:285}}>
            <Select placeholder="Что будем находить?" onChange={this.whChange}>
              <option value="E">Кинетическую энергию</option>
              <option value="V">Скорость</option>
              <option value="m">Массу</option>
            </Select>
            <Input type="text" style = {{marginTop:10}} placeholder={wh1} size="xl" onChange={this.handleChange1}/>  
            <Input type="text" style = {{marginTop:5}} placeholder = {wh2}  size="xl" onChange={this.handleChange2}/>   
            <Button onClick={this.calc} size="xl" className="CalcBtn" mode="commerce">Вычислить</Button>
            <h1 className="CalcsRes">{this.state.result}</h1>
            </Card>
            </Group>
            <Group> <p><strong>E<sub>к</sub> = m*V<sup>2</sup> / 2</strong></p>
            <p><strong>E<sub>к</sub></strong> - кинетическая энергия</p>
            <p><strong>m</strong> - масса</p>
            <p><strong>V</strong> - скорость</p>
            </Group>
        </div>
    );
}  
}


